<template>
  <div class="left-let-page">
    <div id="map"></div>
  </div>
</template>
<script>
import "@/utils/leafletMap/tileLayer.baidu.js";

export default {
  data() {
    return {
      map: null,
    };
  },
  mounted() {
    this.initDate();
  },
  methods: {
    initDate() {
      let map = L.map("map", {
        minZoom: 3,
        maxZoom: 18,
        // center: [39.56, 116.20],
        center: [30.56, 120.2],
        zoom: 8,
        zoomControl: true, // 
        attributionControl: false, // leaflet map logo
        crs: L.CRS.Baidu,
      });
      L.tileLayer.baidu({ layer: "img2" }).addTo(map);
    },
  },
};
</script>
<style scoped lang="scss">
.left-let-page {
  #map {
    width: 100vw;
    height: 100vh;
    margin: auto;
  }
}
</style>
